*{
    margin: 0;
    padding: 0;
  }
  

  .logo p{
    font-size: 25px;
    padding: 20px;
   

  }



  .navbar{
    display: flex;
    justify-content: center;
    
    align-items: center;
    
    width: 100vw;
    height: 100px;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  .list{
    color: white;
    display: flex;
    width: 50%;
    position: relative;
    justify-content: flex-end;
    align-items: center;


  }

  .list ul {
    display: flex;
    justify-content: center;
    align-items: center;
    
    height: 100%;
  }
  .list ul li{
    list-style: none;
    padding: 20px;
    text-align: center;
    width: fit-content;
    cursor: pointer;
    font-size: 15px;
   
  
  
  }
  .menus{
  
    text-decoration: none;
   
    width: 100%;
    cursor: pointer;
    font-size: 20px;
    transition: all 0.2s ease;
  }
 .menus:hover{
   color: blueviolet;
    
  
  }
  


  /* textbox css */
  .container{
   
    width: 100vw;
    height: calc(100vh - 100px);
    display: flex;
    padding-top: 10px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .suct1{
    display: flex;
    flex-direction: column;
    width: 50%;

  }
  .suct1 h1{
    margin: 10px;
    text-align: center;
    width: 100%;
  }
  .box{
    outline: none;
    width: 100%;
    padding: 15px;
    font-size: medium;
    border-radius: 5px;
    text-align: center;
    box-shadow: -1px -1px 10px rgb(75, 73, 73) ;

   

  }
  .btns{
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  .btnup{
    /* width: 20%; */
   
    padding: 8px;
    margin: 10px;
    border-radius: 5px;
    background-color: #2c3e41;
    /* color: black; */
    outline: none;
    border: none;
    cursor: pointer;
    border: 1px solid transparent;

  }

  .themect{
    display: flex;
    align-items: center;
    position: absolute;
    right: 20px;
 
   
  }


  .theme1{
 
    
background-image: linear-gradient(90deg, #102c30 50%, #262525 50%);
   
  }
  .theme2{
 
    
background-image: linear-gradient(90deg, #4118d3 50%, #323333 50%);
   
  }

  .costombtn{
    width: 30px;
    height:30px;
    box-shadow: -1px -1px 2px white;
    margin: 2px;
    

cursor: pointer;
    border-radius: 50%;
    border: 1px solid white;
  }
